.review-row {
  @include desktop {
    padding-right: rem(10px);
    width: 80%;
  }
  @include mobile {
    @include autoMargin;
    width: 100%;
  }
  .review {
    &__list {
      display: grid;
      gap: rem(10px) rem(10px);
    }
    @include desktop {
      &__list {
        grid-template-areas: '. . .';
      }
    }
    @media (max-width: rem(1000px)) {
      &__list {
        grid-template-areas: '. .';
      }
    }
    @media (max-width: rem(600px)) {
      &__list {
        grid-template-areas: '.';
      }
    }
  }
}

.review-column {
  width: 100%;
  .review {
    &__card {
      width: 100%;
    }
  }
}
