header,
footer,
main,
section,
article,
aside,
nav,
figure,
figcaption {
  display: block;
}

body,
body::before,
body::after,
body *,
body *::before,
body *::after {
  box-sizing: border-box;
}

[class] {
  margin: 0;
}

body {
  font-family: "Noto Sans KR", "Roboto", sans-serif;
  font-size: inherit;
  position: relative;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: inherit;
}

ul,
ol {
  padding-left: 0;
  list-style: none;
}

img {
  vertical-align: middle;
}

a {
  color: inherit;
  text-decoration: none;
}

address,
em {
  font-style: normal;
}

fieldset {
  border: 0;
  padding: 0;
  margin: 0;
}

button {
  cursor: pointer;
  color: inherit;
  font-size: inherit;
}

input::-ms-clear,
input::-ms-reveal {
  display: none;
}

input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration {
  display: none;
}

input,
button,
textarea {
  appearance: none;
}

.a11y-hidden {
  overflow: hidden;
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(50%);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.hidden {
  display: none !important;
}

.likes__button {
  border: 0;
  padding: 0;
  background-color: transparent;
}
.likes__button img {
  width: 1.4375rem;
}
.likes__button:hover img {
  animation: hartbit 0.7s ease infinite;
}

@keyframes hartbit {
  0% {
    transform: scale(1);
  }
  70% {
    transform: scale(0.7);
  }
  100% {
    transform: scale(1);
  }
}
.review__card {
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.11);
  border-radius: 0.625rem;
  overflow: hidden;
  margin-bottom: 1.25rem;
  transition: transform 0.2s ease;
}
.review__card:hover {
  transform: translateY(-0.625rem);
}
.review__img {
  cursor: pointer;
  width: 100%;
  height: 55%;
}
.review__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.review__details {
  position: relative;
  background-color: #fff;
  width: 100%;
  padding: 0.875rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: nowrap;
}
.review__details .title {
  cursor: pointer;
  font-weight: 800;
  margin-bottom: 0.625rem;
}
.review__details time {
  color: #929090;
  font-size: 0.6875rem;
}
.review__details .author {
  font-size: 0.875rem;
}
.review__details .title,
.review__details time,
.review__details .author,
.review__details .detail {
  margin-bottom: 0.9375rem;
}
.review__details .detail {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  /* 말줄임 적용 */
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: nowrap;
}
.header__logo a > img {
  width: 100%;
}
@media (min-width: 768px) {
  .header {
    margin: 2.5rem 0;
  }
  .header__logo {
    width: 16rem;
  }
}
@media (max-width: 767px) {
  .header {
    margin: 2.5rem 0 5.625rem;
  }
  .header__logo {
    width: 9.375rem;
  }
}
.header__user-service button {
  border: 0;
  background-color: transparent;
}
@media (min-width: 768px) {
  .header__user-service button,
.header__user-service a {
    margin-left: 0.625rem;
  }
  .header__user-service .new-review {
    padding: 0.375rem 0.625rem;
    min-width: 6.25rem;
    border: 2px solid #525252;
    border-radius: 1.875rem;
  }
  .header__user-service .new-review img {
    display: none;
  }
  .header__user-service img {
    width: 1.5625rem;
  }
  .header__user-service .new-review::before {
    content: "새 리뷰 올리기";
  }
}
@media (max-width: 767px) {
  .header__user-service img {
    width: 1.25rem;
  }
  .header__user-service button,
.header__user-service a {
    margin-left: 0.1875rem;
  }
}

@media (min-width: 768px) {
  .review-row {
    padding-right: 0.625rem;
    width: 80%;
  }
}
@media (max-width: 767px) {
  .review-row {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
  }
}
.review-row .review__list {
  display: grid;
  gap: 0.625rem 0.625rem;
}
@media (min-width: 768px) {
  .review-row .review__list {
    grid-template-areas: ". . .";
  }
}
@media (max-width: 62.5rem) {
  .review-row .review__list {
    grid-template-areas: ". .";
  }
}
@media (max-width: 37.5rem) {
  .review-row .review__list {
    grid-template-areas: ".";
  }
}

.review-column {
  width: 100%;
}
.review-column .review__card {
  width: 100%;
}

.tags {
  border-radius: 0.625rem;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.11);
  background-color: #fff;
}
.tags__title {
  font-size: 1.5625rem;
  font-weight: 800;
  margin-bottom: 1.5625rem;
}
.tags .tag a {
  margin-bottom: 0.3125rem;
  background-color: rgba(160, 197, 101, 0.3);
  padding: 0.625rem;
  border-radius: 0.3125rem;
  display: inline-block;
}
@media (min-width: 768px) {
  .tags {
    width: 20%;
    padding: 1.875rem 1rem;
  }
}
@media (max-width: 767px) {
  .tags {
    position: absolute;
    top: 8.125rem;
    left: 10%;
    padding: 0.625rem;
    padding-right: 0;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    width: 90%;
  }
  .tags__title {
    display: none;
  }
  .tags__list {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
    flex-wrap: nowrap;
    overflow-x: scroll;
  }
  .tags .tag {
    word-wrap: keep-all;
    margin-right: 0.625rem;
  }
  .tags .tag a {
    min-width: 100%;
    white-space: nowrap;
    word-break: break-all;
    display: block;
  }
}

.nav {
  width: 100%;
  margin: 1.875rem 0;
}
.nav__list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: stretch;
  flex-wrap: nowrap;
}
.nav__list li {
  margin-right: 1.25rem;
  padding: 0.5rem;
}
.nav__now {
  border-bottom: 3px solid #000;
}

.container {
  max-width: 1200px;
  padding-left: 0;
  padding-right: 0;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 0.875rem;
  overflow: hidden;
  min-width: 18.75rem;
}

.likes__container {
  position: absolute;
  top: 0.625rem;
  right: 0.625rem;
}

@media (min-width: 768px) {
  .mypage.review-row {
    padding-right: 1.25rem;
    width: 80%;
  }
}
.mypage.review__list {
  display: block;
}
.mypage.review__card {
  width: 100%;
  height: 100%;
}
.mypage.review__img {
  aspect-ratio: 818/381;
}
.mypage.review__details {
  padding: 1.5rem 0.875rem;
}
.mypage.detail, .mypage.time {
  margin-bottom: 0.5rem;
}
.mypage.likes__container {
  top: inherit;
  bottom: 1.5rem;
}
.mypage.likes__button {
  border: 0;
  padding: 0;
  background-color: transparent;
}
.mypage.likes__button img {
  width: 1.4375rem;
}

.reviewDetail {
  max-width: 1000px;
  padding-left: 0;
  padding-right: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0.625rem;
}
@media (max-width: 767px) {
  .reviewDetail {
    padding: 0.875rem;
    width: 100%;
  }
}
.reviewDetail__mainheader {
  margin-bottom: 1.25rem;
}
.reviewDetail__contentWrap {
  border-radius: 0.625rem;
  padding: 2.5rem;
  background: white;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.11);
}
@media (max-width: 767px) {
  .reviewDetail__contentWrap {
    width: 100%;
    padding: 0.625rem;
  }
}
.reviewDetail__title {
  font-size: 3.75rem;
  font-weight: bold;
  padding: 1.25rem 0;
}
@media (max-width: 767px) {
  .reviewDetail__title {
    font-size: 2.125rem;
  }
}
.reviewDetail__header {
  border-bottom: 1px solid #c6c6c6;
  padding-bottom: 1.25rem;
}
@media (max-width: 767px) {
  .reviewDetail__header {
    padding-bottom: 0;
  }
}
.reviewDetail__informWrap {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .reviewDetail__informWrap {
    flex-flow: column;
  }
}
@media (max-width: 767px) {
  .reviewDetail__inform:after {
    display: block;
    content: "";
    width: 100%;
    border-bottom: 1px solid #c6c6c6;
  }
}
.reviewDetail__inform--userid {
  font-size: 1.25rem;
  font-weight: bold;
  padding: 0 0 0.625rem 0;
  display: inline-block;
}
@media (max-width: 767px) {
  .reviewDetail__inform--userid {
    font-size: 1rem;
  }
}
.reviewDetail__inform--beforeDay:before {
  content: "-";
  padding: 0 1.25rem;
}
.reviewDetail__inform--beforeDay {
  font-size: 1rem;
  color: #525252;
}
.reviewDetail__inform--tag {
  margin: 0.625rem 0;
}
.reviewDetail__inform--tag--text {
  display: inline-block;
  padding: 0.375rem;
  background-color: rgba(160, 197, 101, 0.3);
  border-radius: 0.3125rem;
}
.reviewDetail__addInform {
  display: flex;
  flex-flow: column;
}
@media (max-width: 767px) {
  .reviewDetail__addInform {
    flex-flow: row;
    justify-content: space-evenly;
  }
}
.reviewDetail__addInform--ratingWrap {
  padding: 0.625rem 0.9375rem;
  display: flex;
  flex-flow: row;
  align-items: center;
}
@media (max-width: 767px) {
  .reviewDetail__addInform--ratingWrap {
    padding: 0.625rem;
    flex-flow: column;
    align-items: flex-start;
    width: 50%;
  }
}
.reviewDetail__addInform--ratingText {
  font-size: 1.25rem;
  font-weight: bold;
  padding-right: 1.25rem;
}
@media (max-width: 767px) {
  .reviewDetail__addInform--ratingText {
    font-size: 1.25rem;
    padding: 0.625rem 0;
  }
}
.reviewDetail__addInform--starsWrap {
  display: flex;
  flex-flow: row;
  align-items: center;
}
@media (max-width: 767px) {
  .reviewDetail__addInform--starsWrap {
    flex-flow: row;
  }
}
.reviewDetail__addInform--starsCount {
  font-size: 1rem;
  padding-right: 0.625rem;
}
.reviewDetail__addInform--likesWrap {
  padding: 0.625rem 0.9375rem;
  display: flex;
  flex-flow: row;
}
@media (max-width: 767px) {
  .reviewDetail__addInform--likesWrap {
    flex-flow: column;
    align-self: flex-start;
    width: 50%;
  }
}
.reviewDetail__addInform--likesText {
  font-size: 1.25rem;
  font-weight: bold;
  padding-right: 3.125rem;
}
@media (max-width: 767px) {
  .reviewDetail__addInform--likesText {
    padding: 0.625rem 0;
    font-size: 1.25rem;
  }
}
.reviewDetail__addInform--likesSubWrap {
  display: flex;
  flex-flow: row;
  align-items: center;
}
@media (max-width: 767px) {
  .reviewDetail__addInform--likesSubWrap {
    align-items: flex-start;
  }
}
.reviewDetail__addInform--likesCount {
  padding-right: 0.9375rem;
  font-size: 1rem;
}
@media (max-width: 767px) {
  .reviewDetail__addInform--likesCount {
    padding-right: 0.5rem;
  }
}
.reviewDetail__content {
  padding: 1.25rem;
  font-size: 1.25rem;
}
@media (max-width: 767px) {
  .reviewDetail__content {
    padding: 0.375rem;
    font-size: 1.125rem;
  }
}
.reviewDetail__addComments {
  padding: 2.5rem 1.25rem;
}
.reviewDetail__addComments--form {
  text-align: center;
}
.reviewDetail__addComments--count {
  text-align: left;
  font-size: 1rem;
  margin: 1.25rem 0 1.25rem 0.3125rem;
  display: block;
}
.reviewDetail__addComments--input {
  background: white;
  border-radius: 0.625rem;
  padding: 1.25rem;
  width: 98%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.11);
}
.reviewDetail__addComments--btnWrap {
  text-align: right;
}
.reviewDetail__addComments--cancel, .reviewDetail__addComments--confirm {
  border-radius: 0.625rem;
  border: none;
  padding: 0.5rem 1rem;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.11);
}
.reviewDetail__addComments--cancel {
  background: #929090;
  color: white;
  margin: 0.375rem;
}
.reviewDetail__addComments--confirm {
  background: black;
  color: white;
  margin: 0.625rem;
}
.reviewDetail__comments {
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.11);
  border-radius: 0.625rem;
  padding: 1.25rem;
  background: #fff;
}
.reviewDetail__comments--items {
  padding: 0.625rem 0.625rem 0 0.625rem;
  display: flex;
  flex-flow: column;
}
.reviewDetail__comments--items:after {
  display: block;
  content: "";
  width: 100%;
  border-bottom: 1px solid #c6c6c6;
  margin: 0.625rem 0;
}
.reviewDetail__comments--user {
  font-size: 1.25rem;
  font-weight: bold;
}
.reviewDetail__comments--content {
  padding: 0.875rem 0.3125rem;
  font-size: 1rem;
}
.reviewDetail__relatedReview {
  padding: 2.5rem 1.25rem;
}
.reviewDetail__relatedReview--title {
  font-size: 1.5rem;
  text-align: center;
  margin: 2.5rem 0;
}

.review-column-changewidth {
  width: 100%;
  padding-right: 0;
}

.header-search {
  margin: 2.5rem 0;
}

.search-wrap {
  max-width: 700px;
  padding-left: 0;
  padding-right: 0;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: stretch;
  flex-wrap: wrap;
  overflow: hidden;
  width: 100%;
}

.search {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  margin-bottom: 2.5rem;
}
.search__form, .search__message {
  position: relative;
  width: 100%;
}
.search__input {
  width: 100%;
  border: 0;
